*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
}
*::after, *::before{
    box-sizing: border-box;
}
a{
    color: inherit; text-decoration: none;
}
/* }消除a标签样式 */

body{
    background-color: #eee;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
header{
    width: 60vw;
    display: flex;
    justify-content: center;
    align-items: center;
}
header>form{
    width: 100%;
    margin: 20px 20px;
    display: flex;
    
}
header>form>input{
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
    border: none;
    border-radius: 20px 0 0 20px;
}
header>form>button{
    font-size: 16px;
    white-space: nowrap;
    padding: 10px 20px;
    border: none;
    border-radius:0 20px 20px 0;
    background-color: rgb(35, 135, 155);
    color: white;
}

main{
    margin: 15px 20px;
    width: 60vw;
}
main>ul{
    display: flex;
    flex-wrap: wrap;
    
}
main>ul>li{
    background-color: white;
    margin-bottom: 10px;
    margin-right: 10px;
    width: 100px;
    height: 100px;
    border: none;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
main>ul>li>a{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.logo{
    width: 60%;
    height: 60%;
    font-size: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
}
